.form {
  display: flex;
  align-items: center;
  flex-direction: column;

  margin-top: 80px;
  margin-left: auto;
  margin-right: auto;
}

.inputWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.formLabel,
.formName,
.formNumber {
  display: inline;
  align-items: center;
  font-size: large;
  text-align: left;
  padding: 0.5rem 1rem 0.5rem 1rem;
}

.formBtn {
  margin-bottom: 2rem;
  background-color: rgba(23, 26, 32, 0.8);
  border: none;
  border-radius: 6px;
  color: #fff;
  font-family: SFProText-Regular, Helvetica, Arial, sans-serif;
  font-size: 15px;
  height: 36px;
  line-height: 20px;
  margin-right: 6px;
  min-width: 185px;
  padding: 0 16px 0 16px;
  color: white;
  cursor: pointer;

  margin-top: 20px;
}

.formBtn:hover {
  background-color: rgba(23, 26, 32, 1);
  cursor: pointer;
}
